import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';

@Component({
  selector: 'app-reset-pwd',
  templateUrl: './reset-pwd.component.html',
  styleUrls: ['./reset-pwd.component.styl']
})
export class ResetPwdComponent implements OnInit {
  count = 10;
  timer;
  show = true;
  @Input() title = '修改密码';
  validateForm: FormGroup;
  error;

  constructor(
    private authService: AuthService,
    private fb: FormBuilder,
    private router: Router
  ) { }

  ngOnInit(): void {
    const localEmail = localStorage.getItem('email');
    this.validateForm = this.fb.group({
      email: [localEmail, [Validators.required, this.validateEmail]],
      password: [null, [Validators.required, this.validatePass]],
      captcha: [null, [Validators.required, this.validateCaptcha]]
    });
  }


  getCode(): void {
    const TIME_COUNT = 150;
    if (!this.timer) {
      this.count = TIME_COUNT;
      this.show = false;
      this.timer = setInterval(() => {
        if (this.count > 0 && this.count <= TIME_COUNT) {
          this.count--;
        } else {
          this.show = true;
          clearInterval(this.timer);
          this.timer = null;
        }
      }, 1000);
    }
  }

  submitForm(form, ev: Event): void {
    ev.preventDefault();
    console.log(form.value);
    const userInfo = form.value;

    // 验证邮箱，密码是否符合规则
    // this.validateForm.controls.email.setValidators(this.validateEmail);
    // this.validateForm.controls.password.setValidators(this.validatePass);

    // 可设置字母，数字，可以包含@-,._
    const reg = /^[\_0-9a-zA-Z\,\.\-\@]$/;

    // 提交数据
    this.resetPwd();
  }

  goToPage(path): void {
    this.router.navigate([path]);
  }

  validateEmail(c: FormControl): { [key: string]: any } {
    const val = c.value;
    if (!val) {
      return null;
    }

    const reg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
    if (reg.test(val)) {
      return null;
    }

    return {
      error: '邮箱格式不符合校验规则'
    };
  }

  validatePass(c: FormControl): { [key: string]: any } {
    const val = c.value;
    if (!val) {
      return null;
    }

    const reg = /^[a-zA-Z0-9]{8,32}$/;
    if (reg.test(val)) {
      return null;
    }

    return {
      error: '数字，字母，8-32位'
    };
  }

  validateCaptcha(c: FormControl): { [key: string]: any } {
    const val = c.value;
    if (!val) {
      return null;
    }

    const reg = /^[0-9]{6}$/;
    if (reg.test(val)) {
      return null;
    }

    return {
      error: '验证码需为6位数字'
    };
  }

  getCaptcha(): any {
    this.getCode();
    const email = this.validateForm.get('email').value;
    console.log(this.validateForm);
    this.authService.getCaptcha({email}).subscribe(res => {
      console.log(res);
      const {err_no, error} = res;
      this.error = error;
    });
  }

  resetPwd(): any {
    const data = this.validateForm.value;
    this.authService.resetPwd(data).subscribe(res => {
      const {err_no, error} = res;
      this.error = error;

      localStorage.removeItem('email');
    });
  }

}
