import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {environment} from '../../environments/environment';
import { query } from '@angular/animations';

interface UserInfo {
  email: string;
  password: string;
}
@Injectable({
  providedIn: 'root'
})
export class AuthService {
  baseUrl = environment.baseUrl;
  constructor(
    private http: HttpClient
  ) { }

  login(userInfo: UserInfo): any {
    return this.http.post(`${this.baseUrl}/api/auth/login`, userInfo);
  }

  register(userInfo: UserInfo): any {
    return this.http.post(`${this.baseUrl}/api/auth/register`, userInfo);
  }

  getAllUsers(): any {
    return this.http.get(`${this.baseUrl}/api/allUsers`);
  }

  getCaptcha({email}): any {
    return this.http.post(`${this.baseUrl}/api/getCaptcha`, {email});
  }

  resetPwd(data): any {
    return this.http.post(`${this.baseUrl}/api/resetPwd`, data);
  }

  checkEmailExist(data): any {
    return this.http.post(`${this.baseUrl}/api/checkEmail`, data);
  }

  postBookingInfo(data): any {
    return this.http.post(`${this.baseUrl}/api/bookingInfo`, data);
  }

  getBookingInfo(id): any {
    return this.http.get(`${this.baseUrl}/api/bookingInfo?id=${id}`);
  }

  getBookingList(): any {
    return this.http.get(`${this.baseUrl}/api/bookingInfoList`);
  }

}
