import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { AuthService } from 'src/app/services/auth.service';


@Component({
  selector: 'app-booking',
  templateUrl: './booking.component.html',
  styleUrls: ['./booking.component.styl']
})
export class BookingComponent implements OnInit {
  form: FormGroup;
  hotelTypes = [
    {
      type: 1,
      label: '行政大床房 （300元/夜/间）'
    },
    {
      type: 2,
      label: '豪华大床房 （400元/夜/间）'
    },
    {
      type: 3,
      label: '尊享大床房 （500元/夜/间）'
    },
  ];
  constructor(
    private fb: FormBuilder,
    private authService: AuthService,
  ) { }

  ngOnInit(): void {
    this.form = this.fb.group({
      email: [null, [Validators.required]],
      booking_date: [null, [Validators.required]],
      username: [null, [Validators.required]],
      num_of_room: [1, [Validators.required]],
      num_of_people: [1, [Validators.required]],
      hotelDay: [null, [Validators.required]],
      hotelType: [null, [Validators.required]],
      comment: ['无', [Validators.required]],
      phone: [null, [Validators.required]],
    });
  }

  onSubmit(form, ev: Event): void {
    ev.preventDefault();
    // tslint:disable-next-line:forin
    for (const i in this.form.controls) {
      this.form.controls[i].markAsDirty();
      this.form.controls[i].updateValueAndValidity();
    }

    this.postMyBookingInfo();
  }
  /**
   * 获取酒店信息
   *  email
   */
  getMyBookingInfo(email): void {
    this.authService.getBookingInfo(email).subscribe(res => {
      const { error, err_no } = res;

      console.log(res);
    });
  }
  /**
   * 提交酒店信息预订
   */
  postMyBookingInfo(): void {
    const data = this.form.value;
    data.booking_date = data.booking_date.join(';');
    this.authService.postBookingInfo(data).subscribe(res => {
      const {error, err_no} = res;

      console.log(res);
    });
  }
  /**
   * 获取酒店预订所有信息列表
   */
  getBookingList(): void {
    this.authService.getBookingList().subscribe(res => {
      const { error, err_no } = res;

      console.log(res);
    });
  }


  validateNum(c: FormControl): {[key: string]: any} {
    const val = c.value;
    if (!c.value) {
      return null;
    }

    const reg = /^[1-9]$/;


  }

}
