import { Component, OnInit, Input } from '@angular/core';
import {Router} from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.styl']
})
export class RegisterComponent implements OnInit {
  @Input() title = '注册';
  validateForm: FormGroup;
  error;
  isLogin = false;
  emailInfo = '';
  constructor(
    private authService: AuthService,
    private fb: FormBuilder,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.emailInfo = localStorage.getItem('email');
    this.isLogin = !!this.emailInfo;
    if (this.isLogin) {
      return;
    }

    this.validateForm = this.fb.group({
      email: [null, [Validators.required, this.validateEmail]],
      password: [null, [Validators.required, this.validatePass]],
    });
  }

  logout(): void {
    localStorage.removeItem('email');
    this.goToPage('login');

  }

  submitForm(form, ev: Event): void {
    ev.preventDefault();
    console.log(form.value);
    const userInfo = form.value;

    // 验证邮箱，密码是否符合规则
    // this.validateForm.controls.email.setValidators(this.validateEmail);
    // this.validateForm.controls.password.setValidators(this.validatePass);

    // 提交数据
    this.authService.register(userInfo).subscribe(res => {
      const { err_no, error } = res;
      if (!err_no) {
        const data = res.data;
        localStorage.setItem('email', data.email);

        // 注册成功后跳转

        this.goToPage('home');
      }

      this.error = error ? error : '';
    });
  }

  goToPage(path): void {
    this.router.navigate([path]);
  }

  validateEmail(c: FormControl): {[key: string]: any} {
    const val = c.value;
    if (!val) {
      return null;
    }

    const reg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
    if (reg.test(val)) {
      return null;
    }

    return {
      error: '邮箱格式不符合校验规则'
    };
  }

  validatePass(c: FormControl): { [key: string]: any } {
    const val = c.value;
    if (!val) {
      return null;
    }

    const reg = /^[a-zA-Z0-9]{8,32}$/;
    if (reg.test(val)) {
      return null;
    }

    return {
      error: '数字，字母，8-32位'
    };
  }

}
